import React from 'react';
import emailjs from 'emailjs-com';


export default function Email() {

    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('service_k20ac5l', 'template_ut8rcdr', e.target, 'user_UhnAU14trJsp43rydBb7W')
            .then((result) => {
                console.log(result.text);
                document.querySelector('.activeSend').style.display = 'none'
                document.querySelector('.afterSend').style.display = 'inherit'

            }, (error) => {
                console.log(error.text);
            });
    }
    function sendMessage(){
        if (document.querySelector('#user_name').value !== '' &&
            document.querySelector('#user_email').value !== '' &&
            document.querySelector('#message').value !== ''
        ){
            document.querySelector('.beforeSend').style.display = 'none'
            document.querySelector('.activeSend').style.display = 'inherit'
        }
    }

    return (


         <div className='emailWrapper'>

             <form className="contact-form" onSubmit={sendEmail}>
                 <div className='kopa'>
                 <div className='footerText'>
                     <div className="mobMore">Выберите желаемый формат</div>
                     <div >
                         <div className='radioLine'>
                             <input type="radio" id="huey" className="huey" name="user_radio" value="Онлайн консультация"
                                    checked/>
                             <label htmlFor="huey" className='radioText'>Онлайн консультация</label>
                         </div>

                         <div className='radioLine'>
                             <input type="radio" id="dewey" className="huey" name="user_radio" value="Личный приём" />
                             <label htmlFor="dewey" className='radioText'>Личный приём</label>
                         </div>
                     </div>
                 </div>


                <div className='leftSideFoo'>
                    <div className='topBarEmail'>
                        <div className='blockEmail'>
                            {/*<label>Имя</label>*/}
                            <input className='inputEmail' type="text" name="user_name" id='user_name' required='required' placeholder='Имя'/>
                        </div>
                        <div className='blockEmail'>
                            {/*<label>Email</label>*/}
                            <input className='inputEmail' type="phone" name="user_phone" id='user_email' placeholder='Телефон' required='required'/>
                        </div>


                    </div>
                    {/*<label>Сообщение</label>*/}
                    <textarea name="message" className='message' id='message' placeholder='Опишите вашу ситуацию (по желанию)'/>
                    <button type="submit" className='sendEmail' onClick={sendMessage.bind(this)}>
                        <div className='beforeSend'>Отправить заявку</div>
                        <div className='activeSend'>Отправка...</div>
                    </button>

                </div>
                 </div>
             </form>
             <div className='afterSend'>
                 <svg className='chek' width="112" height="112" viewBox="0 0 112 112" fill="none" xmlns="http://www.w3.org/2000/svg">
                     <path d="M56 0C25.088 0 0 25.088 0 56C0 86.912 25.088 112 56 112C86.912 112 112 86.912 112 56C112 25.088 86.912 0 56 0ZM44.8 84L16.8 56L24.696 48.104L44.8 68.152L87.304 25.648L95.2 33.6L44.8 84Z" fill="white"/>
                 </svg>

                 <div className='afterSendText'>
                     <div className='topStText'>Ваша заявка отправлена!</div>
                     <div>Ожидайте звонка в ближайшее время</div>
                 </div>

             </div>
         </div>


    );
}