import React from 'react'
import {NavLink} from "react-router-dom";
const Block = props =>{

    return(
        <div className='stBlock'>
            <a className='imgStat' href={props.url} target='_blank'><img src={props.img}/></a>
            <div className='tag3'>{props.tag3}</div>
            <div className='text3'>{props.text3}</div>
            <a href={props.url} target='_blank' to={props.url} className='stHref'>Прочитать статью</a>
        </div>
    )
}
export default Block